* {
	box-sizing: border-box;
}

.logo{
	display: flex;
	justify-content: center;
	margin-top: 40px;
}

/*custom font*/
@import url(https://fonts.googleapis.com/css?family=Montserrat);

/*basic reset*/
* {margin: 0; padding: 0;}

html {
	background: 
		linear-gradient(rgba(225, 225, 225, 0.9), rgba(238, 37, 98, 0.9));
}

body {
	font-family: montserrat, arial, verdana;
}

p, label{
	font-size: 0.8em;
	text-align: left;
	padding: 5px 0;
	margin-top:8px;
}

label{
	color: gray;
}

// style radio buttons before clicked
input[type='radio']:after {
	width: 15px;
    height: 15px;
    border-radius: 15px;
    top: -2px;
    left: -1px;
    position: relative;
    background-color: #d1d3d1;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
}

// after click radio make it pink
input[type='radio']:checked:after {
	width: 16px;
    height: 16px;
    border-radius: 15px;
    top: -3px;
    left: -1px;
    position: relative;
    background-color: #ee2562;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
}

// dropdown selections
select{
	width: 100%;
	margin: 5px 0;
	height: 30px;
	color: #fff;
	font-size: 13px;
	background-color: #ee2562;
  	border: #FEFEFE 1px solid;
 	-webkit-border-radius: 3px;
 	border-radius: 3px;
}


//form styles---------------------------------------------------------------

#msform {
	width: 400px;
	margin: 40px auto;
	text-align: center;
	position: relative;
	padding-bottom: 90px;
}

#msform fieldset {
	background: white;
	border: 0 none;
	border-radius: 3px;
	box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
	box-sizing: border-box;
	width: auto;
	margin: 20px auto;
	padding: 30px 20px;
	
	/*stacking fieldsets above each other*/
	position: relative;
	overflow: hidden;
	display: none; 

	&.active {
		display: block;
	}
}



/*inputs*/
#msform input, #msform textarea {
	padding: 15px;
	border: 1px solid #ccc;
	border-radius: 3px;
	margin-bottom: 10px;
	width: 100%;
	box-sizing: border-box;
	font-family: montserrat;
	color: #ee2562;
	font-size: 13px;
}

/* previous, next, submit buttons*/
#msform .action-button {
	width: 100px;
	background: #ee2562;
	color: white;
	border: 0 none;
	border-radius: 1px;
	cursor: pointer;
	padding: 10px 5px;
	margin: 10px 5px;
	letter-spacing: 0.07em;
}

#msform .action-button:hover, #msform .action-button:focus {
	box-shadow: 0 0 0 2px white, 0 0 0 3px #fff;
}

/*field heading*/
.fs-title {
	font-size: 15px;
	text-transform: uppercase;
	color: #666;
	margin-bottom: 10px;
}

/*second field heading*/
.fs-subtitle {
	font-weight: normal;
	font-size: 13px;
	color: #666;
	margin-bottom: 20px;
}

/*progressbar*/
#progressbar {
	margin-bottom: 30px;
	overflow: hidden;
	/*CSS counters to number the steps*/
	counter-reset: step;
}

#progressbar li {
	list-style-type: none;
	color: #ee2562;
	text-transform: uppercase;
	font-size: 9px;
	width: 33.33%;
	float: left;
	position: relative;
}

#progressbar li:before {
	content: counter(step);
	counter-increment: step;
	width: 20px;
	line-height: 20px;
	display: block;
	font-size: 10px;
	color: #333;
	background: #fff;
	border-radius: 3px;
	margin: 0 auto 5px auto;
}

/*progressbar connectors*/
#progressbar li:after {
	content: '';
	width: 100%;
	height: 2px;
	background: #ee2562;
	position: absolute;
	left: -50%;
	top: 9px;
	z-index: -1; /*put it behind the numbers*/
}

#progressbar li:first-child:after {
	/*connector not needed before the first step*/
	content: none; 
}

/*marking active/completed steps*/
/*The number of the step and the connector before it = green*/
#progressbar li.active:before,  #progressbar li.active:after{
	background: #ee2562;
	color: #fff;
}

