// age range sliders --------------------------------------------------------
#rangevalue{
	color: #ee2562;
	font-size: 13px;
}

.slidecontainer{
    width: 100%;
}

.slider {
    -webkit-appearance: none;
    width: 100%;
    height: 25px;
    background: #fff;
    outline: none;
}

// pink age slider color
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 27px;
    height: 30px;
    background: #ee2562;
    cursor: pointer;
}

.slider::-moz-range-thumb  {
    width: 27px;
    height: 30px;
    background: #ee2562;
    cursor: pointer;
}


